.train-exit-active {
-webkit-transform: translate(calc(10px + 20vw),0);
        transform: translate(calc(10px + 20vw),0);

   -webkit-transition:   -webkit-transform 800ms cubic-bezier(0.075, 0.82, 0.165, 1);

   transition:   -webkit-transform 800ms cubic-bezier(0.075, 0.82, 0.165, 1);

   transition:   transform 800ms cubic-bezier(0.075, 0.82, 0.165, 1) ;

   transition:   transform 800ms cubic-bezier(0.075, 0.82, 0.165, 1), -webkit-transform 800ms cubic-bezier(0.075, 0.82, 0.165, 1);
}

/* CSS for steam */
.steam {
  position: absolute;
  height: 150px;
  width: 150px;
  border-radius: 50%;
  background-color: #fff;
  margin-top: -75px;
  margin-left: 75px;
  z-index: 0;
  opacity: 0;
}

#steam-one {
  -webkit-animation: steam-one 4s ease-out infinite;
  animation: steam-one 4s ease-out infinite;
}

#steam-three {
  -webkit-animation: steam-one 4s ease-out 1s infinite;
  animation: steam-one 4s ease-out 1s infinite;
}

@-webkit-keyframes steam-one {
  0% {-webkit-transform: translateY(0) translateX(0) scale(0.25);transform: translateY(0) translateX(0) scale(0.25); opacity: 0.2;}
  100% {-webkit-transform: translateY(-200px) translateX(-20px) scale(1);transform: translateY(-200px) translateX(-20px) scale(1); opacity: 0;}
}

@keyframes steam-one {
  0% {-webkit-transform: translateY(0) translateX(0) scale(0.25);transform: translateY(0) translateX(0) scale(0.25); opacity: 0.2;}
  100% {-webkit-transform: translateY(-200px) translateX(-20px) scale(1);transform: translateY(-200px) translateX(-20px) scale(1); opacity: 0;}
}

#steam-two {
  -webkit-animation: steam-two 4s ease-out 0.5s infinite;
  animation: steam-two 4s ease-out 0.5s infinite;
}

#steam-four {
  -webkit-animation: steam-two 4s ease-out 1.5s infinite;
  animation: steam-two 4s ease-out 1.5s infinite;
}

@-webkit-keyframes steam-two {
  0% {-webkit-transform: translateY(0) translateX(0) scale(0.25);transform: translateY(0) translateX(0) scale(0.25); opacity: 0.2;}
  100% {-webkit-transform: translateY(-200px) translateX(20px) scale(1);transform: translateY(-200px) translateX(20px) scale(1); opacity: 0;}
}

@keyframes steam-two {
  0% {-webkit-transform: translateY(0) translateX(0) scale(0.25);transform: translateY(0) translateX(0) scale(0.25); opacity: 0.2;}
  100% {-webkit-transform: translateY(-200px) translateX(20px) scale(1);transform: translateY(-200px) translateX(20px) scale(1); opacity: 0;}
}

 
